        // 
        var $tool: Dance.Tool = {}  
        var raphael = this.raphael
        var ctX = 5, 
            ctY = 5,
            ctW = 75,
            ctH = 300,
            x = ctX, y = ctY,            // 
            config = this.config,
            pkgClr = config.pkgClr
        
        //             
        // var dragHandlerEvnt = function(){}

        // 
        $tool.containerIst = raphael.rect(ctX, ctY, ctW, ctH)
        $tool.containerIst.attr('fill', '#ffffff')      // 

        var nodeIstQue: any[] = [],
            ist: NodeAbstract

        // 
        x += 20, y += 50
