///<reference path="../../index.d.ts"/>
///<reference path="../types/raphael.ts"/>
/**
 * 
 * 
 * @export
 * @abstract
 * @class NodeAbstract
 */
export default abstract class NodeAbstract{
    fromLine: NodeAbstract[]    // 
    toLine: NodeAbstract[]      // 
    NodeType: string            // 
    opt: rSu.NodeOpt            // 
    label?: RaphaelElement      // 
    c?: RaphaelElement          // 
    paper: RaphaelPaper         // Raphael 
    constructor(paper: RaphaelPaper, opt?: rSu.NodeOpt){
        this.paper = paper
        // 
        this.fromLine = []
        this.toLine = []
        this.NodeType = null            // 
        // 
        if(opt){
            this.opt = opt
        }
        this._onInit()
    }
    /**
     * 
     * @param opt 
     */
    creator(opt?: rSu.NodeOpt){
        this._whenCreatorEvt()
        return this
    }
    /**
     * path
     * @param {array} pQue 
     * @param {bool} isClose 
     * @returns {string}
     */
    protected _ps2Path(pQue: rSu.P[], isClose?: boolean): string{
        var path = ''
        for(var i=0; i<pQue.length; i++){
            path += (path? 'L': 'M') + pQue[i].x + ',' + pQue[i].y
        }
        if(isClose){
            path += 'Z'
        }        
        return path
    }
    /**
     * 
     * @param {array} pQue 
     * @param {bool} isClose 
     * @returns {string}
     */
    protected _ps2PathAttr(pQue: rSu.P[], isClose?: boolean){
        var pArr: any[] = []
        for(var i=0; i<pQue.length; i++){
            var cPArr: any[] = ['L']
            if(pArr.length == 0){
                cPArr[0] = 'M'
            }
            cPArr.push(pQue[i].x, pQue[i].y)
            pArr.push(cPArr)
        }
        if(isClose){
            pArr.push(['Z'])
        }        
        return pArr
    }
    /**
     * 
     */
    protected _whenCreatorEvt(){}
    /**
     *  []
     */
    protected _onInit(){}
    /**
     *  []
     */
    onDrag(){}
    /**
     * 
     * @param {stirng} type 
     * @param {this}  $node 
     */
    recordLine(type: string, $node: any){
        if('from' == type){
            this.fromLine.push($node)
        }
        else if('to' == type){
            this.toLine.push($node)
        }
    }
    /**
     * 
     * @param {function} callback 
     */
    syncLineMove(callback: any){
        if('function' !== typeof callback){
            callback = (instance: any, type: any) => {}
        }
        // 
        var fLines = this.fromLine        
        var tLines = this.toLine
        // 
        for(var i=0; i<fLines.length; i++){
            var $fC = fLines[i].c
            var $fPath = $fC.attr('path')
            callback($fC, 'from', fLines[i])
        }
        // 
        for(var j=0; j<tLines.length; j++){
            var $tC = tLines[j].c
            callback($tC, 'to', tLines[j])
        }
    }
    /**
     * 
     * NodeBase struct to json   struct 
     * @returns {object}
     */
    toJson(){
        var _struct: Flower.StepStru = {
            NodeType: this.NodeType,            // 
            opt: this.opt,                      // 
            c: {
                attr: this.c.attr()              // 
            }            
        }
        if(this.label){                         // 
            _struct.label = {
                attr: this.label.attr()
            }
        }
        return _struct
    }
    /**
     * 
     */
    getPLen(P1: rSu.P, P2: rSu.P): number{
        return Math.pow(
            (Math.pow((P1.x - P2.x), 2) + Math.pow((P1.y - P2.y), 2)),
            1/2
        )
    }
    /**
     * 
     * @param nOpt 
     */
    protected _updAttr(nOpt: rSu.NodeOpt){
        for(var key in nOpt){
            if('undefined' != typeof this.opt[key]){
                this.opt[key] = nOpt[key]
            }
        }
        return this
    }
    /**
     * 
     * @returns {boolean}
     */
    delete(): boolean{
        return false
    }
    /**
     * 
     * @returns 
     * @memberof NodeAbstract
     */
    moveable(){
        return this
    }
    /**
     * 
     * @param nOpt 
     */
    updAttr(nOpt: rSu.NodeOpt): any{
        return this
    }
}


